.button {
  position: relative;
  padding: 10px 0;
  margin-top: 30px;
  width: 270px;
  max-width: 270px;
  font-family: Avenir, sans-serif;
  font-size: 20px;
  font-weight: bold;
  color: var(--button-link-color);
  border: 1px solid var(--button-link-color);
  background: white;
  border-radius: 10px;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  overflow: hidden;
  outline: none;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}

.button::after {
  position: absolute;
  top: 23px;
  right: 13px;
  width: 7px;
  height: 1px;
  -webkit-transition: width 0.3s;
  transition: width 0.3s;
  background: var(--button-link-color);
  content: '';
}

.button:hover::after {
  width: 30px;
}

@-webkit-keyframes fade-in {
  from {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

@keyframes fade-in {
  from {
    opacity: 0;
    -webkit-transform: translateY(20px);
            transform: translateY(20px);
  }
  to {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
}

.fade-enter-active,
.fade-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

.fade-enter,
.fade-leave-to {
